/* Γενικές ρυθμίσεις για να μοιάζει με απλή HTML σελίδα */
.App {
  font-family: Arial, Helvetica, sans-serif;
  max-width: 950px;
  margin: 20px auto;
  padding: 10px;
  color: #333;
}

h1 {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
}

/* Τα κουτιά των ενοτήτων - Απλό γκρι περίγραμμα όπως στην εκφώνηση */
section {
  border: 1px solid #ccc;
  margin-bottom: 25px;
  padding: 0; /* Το padding μπαίνει στα εσωτερικά στοιχεία */
}

/* Οι τίτλοι των ενοτήτων (Header) */
section h2 {
  background-color: #f8f9fa; /* Ελαφρύ γκρι φόντο στον τίτλο */
  margin: 0;
  padding: 10px 15px;
  font-size: 18px;
  border-bottom: 1px solid #ccc;
}

/* Περιεχόμενο μέσα στα κουτιά */
section > p, section > div {
  padding: 10px 15px;
}

/* Η φόρμα δημιουργίας και τα inputs */
.input-group {
  display: flex;
  gap: 10px;
  align-items: center;
  flex-wrap: wrap;
  padding: 15px;
}

input {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
  min-width: 150px;
}

/* Κουμπιά - Χρώματα παρόμοια με τις φωτογραφίες */
button {
  padding: 6px 12px;
  cursor: pointer;
  border: 1px solid #999;
  border-radius: 3px;
  font-size: 14px;
  background-color: #e0e0e0; /* Default γκρι */
}

button:hover {
  opacity: 0.9;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
  color: #666;
}

/* Ειδικά χρώματα κουμπιών όπως στην εικόνα */
/* Create -> Μπλε */
button:not(:disabled) {
    color: black;
}

/* Το στοιχείο της λίστας δημοπρασιών (Row style αντί για Card) */
.auction-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eee; /* Διαχωριστική γραμμή αντί για κουτί */
  padding: 15px;
  background-color: #fff;
}

.auction-card:last-child {
  border-bottom: none;
}

.auction-card h3 {
  font-size: 16px;
  margin: 0 0 5px 0;
}

.auction-card p {
  margin: 2px 0;
  font-size: 13px;
  color: #555;
}

/* Status μηνύματα */
.status {
  padding: 10px;
  background-color: #e6f7ff;
  border: 1px solid #91d5ff;
  margin-bottom: 20px;
  text-align: center;
}

.error {
  color: red;
  font-size: 12px;
  margin-top: 5px;
}